// @ts-nocheck

import React, { useState, useEffect, useMemo } from 'react'
import { Ellipsis } from 'react-spinners-css'

import { callApisOpenBanking } from '../../services/callApisOpenBanking'
import { getBanksOfApi } from '../../utils/getBanksOfApi'
import { fixMoney } from '../../utils/fixMoney'
import { useModal } from '../../hooks/Modal'

import ComparisonMatrix from '../../components/ComparisonMatrix'
import Layout from '../../components/Layout/Layout'
import {
	CompanyStyled,
	MatrixPageStyled,
	TableStyled,
} from '../../styles/CallApiPage.styled'

interface IResponsePersonalCreditCard {
	name: string
	companies: ICompanies[]
}

interface ICompanies {
	name: string
	cnpjNumber: string
	urlComplementaryList: string
	personalCreditCards: IPersonalCreditCards[]
}

interface IPersonalCreditCards {
	name: string
	identification: {
		product: {
			type: string
			additionalInfo: string
		}
		creditCard: {
			network: string
			additionalInfo: string
		}
	}
	rewardsProgram: {
		hasRewardProgram: boolean
		rewardProgramInfo: string
	}
	fees: {
		services: IService[]
	}
	interest: IInterest[]
	termsConditions: {
		minimumFeeRate: string
		additionalInfo: string | null
		elegibilityCriteriaInfo: string
		closingProcessInfo: string
	}
}

interface IService {
	name: string
	code: string
	chargingTriggerInfo: string
	prices: IPrice[]
	minimum: {
		value: string
		currency: string
	}
	maximum: {
		value: string
		currency: string
	}
}

interface IPrice {
	interval: string
	value: string
	currency: string
	customers: {
		rate: number
	}
}

const PersonalCreditCardsPage = () => {
	const [state, setState] = useState<IResponsePersonalCreditCard[]>([])
	const { addMoreInfo } = useModal()

	const endpoint = '/personal-credit-cards'
	const banks = getBanksOfApi(endpoint)

	// Realiza as consultas s APIs
	useEffect(() => {
		;(async () => {
			const apiResponses = await callApisOpenBanking('/personal-credit-cards')

			setState(apiResponses)
		})()
	}, [])

	// Organiza as informaes para serem lidas pela matriz
	const typesState = useMemo(() => {
		let services = []
		let servicesList = []
		let companies = []
		state.forEach((brand) =>
			brand.companies.forEach((company) => {
				company.personalCreditCards.forEach((creditcard) => {
					creditcard.fees.services.forEach(({ code, minimum, maximum }) => {
						const fare = {
							code,
							name: creditcard.name,
							minimum: minimum.value,
							maximum: maximum.value,
						}

						companies[company.name]
							? companies[company.name].push(fare)
							: (companies[company.name] = [fare])

						!servicesList.includes(code) && servicesList.push(code)
					})
				})
				servicesList.forEach((serviceCode) => {
					const servicePayload = company.name !== 'INDISPONVEL' && {
						...companies[company.name]
							.filter((fare) => fare.code === serviceCode)
							.map((fare) => {
								return {
									name: fare.name,
									minimum: fare.minimum,
									maximum: fare.maximum,
								}
							}),
						brand: brand.name,
						company: company.name,
					}
					services[serviceCode]
						? services[serviceCode].push(servicePayload)
						: (services[serviceCode] = [servicePayload])
				})
			})
		)

		return services
	}, [state])

	return (
		<Layout title="Comparativo - Carto de Crdito para pessoa fsica">
			<MatrixPageStyled>
				<h3>
					Estas so todas as tarifas dos servios de carto de crdito para pessoa fsica
					dos bancos:&nbsp;
					{banks.map((bank, index) =>
						index === 0 ? `${bank.brandName}` : `, ${bank.brandName}`
					)}
					.
				</h3>
				{state.length > 0 && (
					<ComparisonMatrix
						banks={banks}
						stateCompanies={banks
							.map((bank) => bank.brandName)
							.map(
								(requiredBrand) => state.filter((brand) => brand.name === requiredBrand)[0]
							)
							.map(({ companies }) =>
								companies.map(({ name }) => {
									return name
								})
							)
							.map((array) => Object.values(array))}
						typesState={typesState}
						fixFunction={fixMoney}
					/>
				)}
				{Object.keys(typesState).length === 0 && <Ellipsis color="#3E446C" />}
				<h3 style={{ marginTop: '50px' }}>
					Tabelas completas com as taxas de carto de crdito para pessoa fsica.
				</h3>
				{state &&
					state.map((brand, index) =>
						brand.companies.map((company) => (
							<CompanyStyled key={`company${company.name}${index}`}>
								<div>
									<span>{brand.name}</span>{' '}
									<a
										href={brand.companies[0].urlComplementaryList}
										target="_blank"
										rel="noopener noreferrer"
										className="blue"
									>
										{brand.companies[0].name}
									</a>{' '}
									<span>CNPJ: {brand.companies[0].cnpjNumber}</span>
								</div>
								<TableStyled>
									<thead>
										<tr>
											<th>Servio</th>
											<th>Carto</th>
											<th>Emissor</th>
											<th>Mn</th>
											<th>Mx</th>
											<th>Informaes</th>
										</tr>
									</thead>
									<tbody>
										{company.personalCreditCards &&
											company.personalCreditCards.map(
												({ identification, fees, termsConditions }) =>
													fees.services.map(
														(
															{ name, code, chargingTriggerInfo, minimum, maximum },
															index
														) => (
															<tr key={`${code}`}>
																<td>{code.replace(/[_\s]/g, ' ')}</td>
																<td>
																	{identification.product.type.replace(/[_\s]/g, ' ')}
																</td>
																<td>
																	{identification.creditCard.network.replace(/[_\s]/g, ' ')}
																</td>
																<td key={`min${index}`}>{fixMoney(minimum.value)}</td>
																<td key={`max${index}`}>{fixMoney(maximum.value)}</td>
																<td>
																	<button
																		onClick={() =>
																			addMoreInfo({
																				name,
																				chargingTriggerInfo,
																				termsConditions,
																			})
																		}
																		className="blue"
																	>
																		Mais Informaes
																	</button>
																</td>
															</tr>
														)
													)
											)}
									</tbody>
								</TableStyled>
							</CompanyStyled>
						))
					)}
			</MatrixPageStyled>
		</Layout>
	)
}

export default PersonalCreditCardsPage
